@media (max-width: 400px){
    


    .styled{
        width: 70%;
        margin-top: 50px;
        margin-left: 90px;    
    }
    
    .containsection{
        display: grid;
        grid-template-columns: 1fr;
        width: 120px;
        gap: 30px;    
        margin: 30px auto;
        margin-bottom: 0px;
    }
    .styled1{
        border:solid;
        display:flex;
        justify-content: space-between;
        margin-bottom: 80px;
    }
    .styled1:hover{
        transform: scale(1.03);
    }
    .ProductCard{
        
        border: solid;
        border-radius: 10%;
        display: flex;
        flex-direction: column;
        margin: 0px;
        width: 100px;
        align-items: center;
        justify-content: center;
        > p{
            margin: 2px;
            font-size: 20px;
            font-weight: 600;
        }
        > h4{
            margin:3px
        }
        > button{
            margin:3px;
            border: none;
            background-color: chocolate;
            border-radius: 20%;
            height: 50px;
            font-weight: bold;
        }
        > button:hover{
            opacity: .7;
        }
        > img{
            padding-top: 5px;
            width: 90%;
            height: 120px;
            object-fit: contain;
        }
    }
    .ProductCard:hover{
        transform: scale(1.03);
    }
    
    
    
    }